import request from '@/plugin/axios';
export default {
  name: 'department-list',
  data: function data() {
    return {
      filename: __filename,
      loading: true,
      query: {
        depName: ''
      },
      create: {
        id: '',
        depName: '',
        depDesc: ''
      },
      pagination: {
        currentPage: 1,
        pageSize: 20,
        pageSizes: [10, 20, 30, 50, 100],
        total: 0
      },
      tableData: [],
      formLabelWidth: '80px',
      dialogFormVisible: false
    };
  },
  created: function created() {
    this.getTableData();
  },
  watch: {
    'pagination.currentPage': function paginationCurrentPage(val) {
      this.getTableData();
    },
    'pagination.pageSize': function paginationPageSize(val) {
      this.getTableData();
    }
  },
  methods: {
    handleEdit: function handleEdit(row) {
      var _this = this;

      if (row.id) {
        this.create.id = row.id;
        request.get("/department/".concat(row.id)).then(function (res) {
          _this.create = res.data;
        });
        this.dialogFormVisible = true;
      }
    },
    search: function search() {
      this.pagination.currentPage = 1;
      this.getTableData();
    },
    addDep: function addDep() {
      // 清空数据
      this.create.id = '';
      this.dialogFormVisible = true;
      this.$refs['create'].resetFields();
    },
    reset: function reset(formName) {
      this.$refs[formName].resetFields();
      this.getTableData();
    },
    handleSizeChange: function handleSizeChange(val) {
      this.pagination.pageSize = val;
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pagination.currentPage = val;
    },
    getTableData: function getTableData() {
      var _this2 = this;

      this.loading = true;
      var page = this.pagination;
      var params = {
        'index': page.currentPage,
        'size': page.pageSize
      };
      var filters = {
        'name': this.query.depName
      };
      params.filters = filters;
      request.get('/department', {
        params: params
      }).then(function (res) {
        _this2.pagination.total = res.data.count;

        if ((_this2.pagination.currentPage - 1) * _this2.pagination.pageSize >= _this2.pagination.total) {
          _this2.pagination.currentPage--;
          return;
        }

        _this2.tableData = res.data.list;
        _this2.loading = false;
      }).catch(function () {
        _this2.loading = false;
      });
    },
    save: function save() {
      var _this3 = this;

      var params = JSON.parse(JSON.stringify(this.create));
      request.post('/department', params).then(function (res) {
        if (res.code === 0) {
          _this3.dialogFormVisible = false;

          _this3.$refs['create'].resetFields();

          _this3.getTableData();
        }
      });
    },
    deleteDep: function deleteDep(row) {
      var _this4 = this;

      request.delete("/department/".concat(row.id)).then(function (res) {
        if (res.code === 0) {
          _this4.getTableData();

          _this4.$message({
            message: '删除成功',
            type: 'success'
          });
        }
      });
    }
  }
};